import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { RestaurationPage } from './restauration.page';

const routes: Routes = [
  {
    path: '',
    component: RestaurationPage
  },
  {
    path: 'shopping',
    loadChildren: () => import('./shopping/shopping.module').then( m => m.ShoppingPageModule)
  },
  {
    path: 'historique-restaurant',
    loadChildren: () => import('./historique-restaurant/historique-restaurant.module').then( m => m.HistoriqueRestaurantPageModule)
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class RestaurationPageRoutingModule {}
